var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('articleOuter',[_c('articleTitle',[_vm._v("網站架設接案，四個實際接案範例詳解過程")]),_vm._v(" "),_c('articleTag',{attrs:{"date":'2022.01.27',"tag":'網站接案',"link":'/articles'}}),_vm._v(" "),_c('articlePre',[_vm._v("\n    本文將以四個實際網站架設接案為例，詳解接案過程中，實際會執行的流程，以及各式臨時狀況。我會從案件聯繫、確認需求、談價、出設計稿、程式開發、網站佈署一路說明，給有意製作個人網站、活動策展網站、企業官方網站、後台系統，或想知道接案流程的人作為參考。\n  ")]),_vm._v(" "),_c('articleParagraph',[_vm._v("這是網站接案範例的第二篇文，對本系列有興趣，可以一併看第一篇文"),_c('router-link',{attrs:{"to":"/article/web-frontend-case-study-4","target":"_blank"}},[_vm._v("《網站設計與前端工程，接案實作四例》")]),_vm._v("，分別是不同案件的詳細過程介紹。")],1),_vm._v(" "),_c('articleParagraph',[_vm._v("\n    我本身從事網站接案多年，在此篇介紹的四個案件中，兩個是透過朋友轉介紹的案子，另外兩個是與夥伴創業的網站。其中，有的業主完全不熟悉網站，也有和業主方的專業設計師合作經驗，製作過程中碰到的難題與設計方式，有相當大的差異。\n  ")]),_vm._v(" "),_c('articleParagraph',[_vm._v("本篇介紹的四個接案網站，分別是 Reborn、雅硯、雞籠怪奇、實境解謎遊戲。以下就來一一介紹：")]),_vm._v(" "),_c('articleSub',[_vm._v("Reborn UI/UX 設計課程官方網站－接案實例")]),_vm._v(" "),_c('articleImg',{attrs:{"link":require('@/assets/img/engineer/article-reborn.webp')}},[_vm._v("Reborn UI/UX 設計課程官方網站。")]),_vm._v(" "),_c('articleParagraph',[_vm._v("我在 2020 年初離開正職工作，剛開始擔任自由工作者時，我在個人臉書頁面向臉友表示自己的新狀態後，當時 Reborn 創辦人之一的一粒，很快聯繫上我。")]),_vm._v(" "),_c('articleParagraph',[_vm._v("一粒本身是相當專業的資深設計師，我們是在網路上交流認識。當時他與另一位創辦人剛草創 Reborn，Reborn 一間專門做 UI/UX 設計教學的公司，正在找尋適合工程師合作製作網站。\n  ")]),_vm._v(" "),_c('articleParagraph',[_vm._v("透過遠端會議談妥合作後，我們開始固定時間開會。團隊中，也慢慢加入其他設計師、插畫師、後端工程師等夥伴。")]),_vm._v(" "),_c('articleParagraph',[_vm._v("正式製作網站時，我是以每月收固定費用的方式進行，由於公司本身在做 UI/UX 設計教學，因此對網站設計細節相當注重。團隊為了做出符合公司調性的網站，光在我製作期間，總共就更換過三次設計師。\n  ")]),_vm._v(" "),_c('articleParagraph',[_vm._v("在最後的完稿中，是一粒親自出馬和我一個個比對設計細節，從桌面至手機版本，每一區塊元素都講求對齊、等寬，以及完美。")]),_vm._v(" "),_c('articleParagraph',[_vm._v("Reborn 網站本身，並沒有過於複雜的功能或版型，前端工程的製作速度很快。我們多數時間，都是在針對版型設計與文字細節進行調整。")]),_vm._v(" "),_c('articleParagraph',[_vm._v("我們最後實現出來的功能，除了基本版面外，還有會員登入、金流繳費串接等功能，並將網站架設於 AWS 機器上。")]),_vm._v(" "),_c('articleParagraph',[_vm._v("整體而言，我